import React, { useEffect } from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
import './WaitingForModeration.scss';
import jerm from '../../assets/jerm.svg';
import { useTelegram } from '../../hooks/useTelegram';

function WaitingForModeration() {
    const { t } = useTranslation();
    const { tg } = useTelegram();

    useEffect(() => {
        if (tg.BackButton.isVisible) {
            tg.BackButton.hide();
        }
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }
    }, [])
    return (
        <div className="waiting-for-moderation">
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h1>{t('moderation_title')}</h1>
            <p>{t('moderation_text')}</p>
            <div className="loading-indicator"></div>
        </div>
    );
}

export default WaitingForModeration;
