import React from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
// import { useTelegram } from "../../hooks/useTelegram";
import './TechnicalWorks.scss'; // Import SCSS file for styling
import jerm from '../../assets/jerm.svg';

const TechnicalWorks = () => {
    const { t } = useTranslation();
    // const { tg, user } = useTelegram();
    return (
        <div className="technical-works">
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h2>{t("tech")}</h2>
            <div className="spinner"></div>
        </div>
    );
};

export default TechnicalWorks;
