import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from '../../contexts/TranslationContext';
import ThemeSwitcher from '../../components/ThemeSwitcher';
import { useTelegram } from "../../hooks/useTelegram";
import jerm from '../../assets/jerm.svg';
import { useAuth } from '../../contexts/auth';
// import HandwritingEffect from '../../components/HandwritingEffect'
import './Home.scss';

function Home() {
    const navigate = useNavigate();
    const { tg, user } = useTelegram();

    const { t, setLanguage, language } = useTranslation();

    useEffect(() => {
        const languageCode = localStorage?.getItem('lang') || user?.language_code.toLowerCase();
        if (['hy', 'ru', 'en'].includes(languageCode) && languageCode !== language) {
            setLanguage(languageCode);
        }
    }, [setLanguage, language]);

    useEffect(() => {
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }
        if (tg.BackButton.isVisible) {
            tg.BackButton.hide();
        }
        if (tg.SettingsButton.isVisible) {
            tg.SettingsButton.hide();
        }
    }, [tg]);

    const handleLanguageChange = (event) => {
        setLanguage(event.target.value);
    };

    const handleRegisterClick = () => {
        navigate('/register');
    };

    return (
        <div className="home">
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            {/* <HandwritingEffect /> */}
            <div className='welcome_description'>
                <p>{t('welcome1')}</p>
                <p>{t('welcome2')}</p>
            </div>
            <div className='nav'>
                <button className="register-button" onClick={handleRegisterClick}><span>{t('register')}</span></button>
                <div className="language-selector-container">
                    <select className="language-selector" value={language} onChange={handleLanguageChange}>
                        <option value="en">English</option>
                        <option value="ru"></option>
                        <option value="hy"></option>
                    </select>
                    <ThemeSwitcher />
                </div>
            </div>
            <div className='since'>
                <p>Jerm Inc. All rights reserved.</p>
                <p>Since 2024 </p>
                <p>version 2.2</p>
            </div>
        </div>
    );
}

export default Home;
