import React, { useEffect, useState } from 'react';

function CitySelect({ formData, handleChange, t, countryCityData }) {
    const [cities, setCities] = useState([]);

    useEffect(() => {
        if (formData.country) {
            const selectedCountry = countryCityData.find(country => country.country === formData.country);
            if (selectedCountry) {
                setCities(selectedCountry.cities);
            } else {
                setCities([]);
            }
        }
    }, [formData.country, countryCityData]);

    return (
        <div>
            <label htmlFor="city">{t('profile_city')}</label>
            <select id="city" name="city" value={formData.city || "Yerevan"} onChange={handleChange} required>
                {cities.map((city) => (
                    <option key={city} value={city}>
                        {city}
                    </option>
                ))}
            </select>
        </div>
    );
}

export default CitySelect;
