import Home from './pages/Home/Home';
import RegistrationForm from './pages/Registration/RegistrationForm';
import PhotoUpload from './pages/PhotoUpload/PhotoUpload';
import VerificationPage from './pages/Verification/VerificationPage';
import SelfieUpload from './pages/Selfie/SelfieUpload';
import WaitingForModeration from './pages/Moderation/WaitingForModeration';
import UserProfile from './pages/Profile/UserProfile';
import Swipes from './pages/Swipe/SwipeCards';
import TechnicalWorks from './pages/TechnicalWorks/TechnicalWorks';
import Match from './pages/Match/Match';
import Likes from './pages/Likes/Likes';
import Mutuals from './pages/Likes/Mutuals';
import { withNavigationWatcher } from './contexts/navigation';

const routes = [
    {
        path: '/',
        element: Home
    },
    {
        path: '/tech',
        element: TechnicalWorks
    },
    {
        path: '/register',
        element: RegistrationForm
    },
    {
        path: '/photo-upload',
        element: PhotoUpload
    },
    {
        path: '/verification',
        element: VerificationPage
    },
    {
        path: '/selfie-upload',
        element: SelfieUpload
    },
    {
        path: '/waiting-for-moderation',
        element: WaitingForModeration
    },
    {
        path: '/profile',
        element: UserProfile
    },
    {
        path: '/swipes',
        element: Swipes
    },
    {
        path: '/match',
        element: Match
    },
    {
        path: '/likes',
        element: Likes
    },
    {
        path: '/mutuals',
        element: Mutuals
    }
];

export default routes.map(route => {
    return {
        ...route,
        element: withNavigationWatcher(route.element, route.path)
    };
});
