import React, { useState, useEffect, useRef } from 'react';
import { useSwipeable } from 'react-swipeable';
import './PhotoModal.scss';

const PhotoModal = ({ photos, onClose }) => {
    const [currentPhoto, setCurrentPhoto] = useState(0);
    const [loaded, setLoaded] = useState(false);
    const [swipeDirection, setSwipeDirection] = useState('');
    const modalRef = useRef(null);

    const handlers = useSwipeable({
        onSwipedLeft: () => handleSwipe('next'),
        onSwipedRight: () => handleSwipe('prev'),
    });

    const handleSwipe = (direction) => {
        setSwipeDirection(direction);
        setLoaded(false);
        setTimeout(() => {
            if (direction === 'next') {
                setCurrentPhoto((currentPhoto + 1) % photos.length);
            } else {
                setCurrentPhoto((currentPhoto - 1 + photos.length) % photos.length);
            }
            setSwipeDirection('');
        }, 200); // Duration of the swipe animation
    };

    const handleClickOutside = (event) => {
        if (modalRef.current && !modalRef.current.contains(event.target)) {
            onClose();
        }
    };

    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    useEffect(() => {
        setLoaded(true);
    }, [currentPhoto]);

    return (
        <div {...handlers} className="photo-modal">
            <div ref={modalRef} className="photo-modal-content">
                <button className="close-button" onClick={onClose}></button>
                <img
                    src={photos[currentPhoto]}
                    alt="photo"
                    className={`modal-photo ${loaded ? 'loaded' : ''} ${swipeDirection === 'next' ? 'swipe-left' : swipeDirection === 'prev' ? 'swipe-right' : ''}`}
                    onLoad={() => setLoaded(true)}
                />
                <div className="photo-dots">
                    {photos.map((_, index) => (
                        <span
                            key={index}
                            className={index === currentPhoto ? 'active' : ''}
                            onClick={() => {
                                setCurrentPhoto(index);
                                setLoaded(false);
                            }}
                        ></span>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default PhotoModal;
