import React, { useState, useEffect, useCallback } from 'react';
import { useSwipeable } from 'react-swipeable';
import { useTelegram } from '../../hooks/useTelegram';
import { useNavigate } from 'react-router-dom';
import { getUser, getAllUsers, addReaction } from '../../router/apiServiceFetch';
import { useTranslation } from '../../contexts/TranslationContext';
import { useAuth } from '../../contexts/auth';
import jerm from '../../assets/jerm.svg';
import Card from './Card';
import './SwipeCards.scss';

const SwipeCards = () => {
    const { userBase } = useAuth();
    const num = 3
    const { t } = useTranslation();
    const [currentIndex, setCurrentIndex] = useState(0);
    const [pageNumber, setPageNumber] = useState(1);
    const [current, setCurrent] = useState(1);
    const [swipeClass, setSwipeClass] = useState('');
    const [users, setUsers] = useState([]);
    const [nextClass, setNextClass] = useState('fade-in');
    const [dislikeTapped, setDislikeTapped] = useState(0);
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const { tg, user } = useTelegram();

    //const [user, setUser] = useState({ id: 6256940687 });

    const loadUsers = async () => {
        try {
            setLoading(true);
            const { country, city } = userBase;

            const newUsers = await getAllUsers(user.id, pageNumber, num);
            if (newUsers.length === 0) {
                setCurrent(1);
                setUsers([]);
            } else {
                setUsers(newUsers);
                setCurrentIndex(0);
            }

            setLoading(false);
        } catch (error) {
            setLoading(false);
            setUsers([]);
            console.error('Error fetching users:', error);
        }
    };

    useEffect(() => {
        loadUsers();
    }, []);

    const handleSwiped = (direction) => {
        if (direction === 'Left') {
            handleDislike();
        } else if (direction === 'Right') {
            handleLike();
        }
    };

    useEffect(() => {
        if (tg.BackButton.isVisible) {
            tg.BackButton.hide();
        }
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }

        if (!tg.SettingsButton.isVisible) {
            tg.SettingsButton.show();
        }
    }, [tg]);

    const onSettings = useCallback(() => {
        navigate('/profile');
    }, [navigate]);

    useEffect(() => {
        tg.onEvent('settingsButtonClicked', onSettings);
        return () => {
            tg.offEvent('settingsButtonClicked', onSettings);
        };
    }, [onSettings, tg]);

    const handleReaction = async (reactionType) => {
        const currentUser = users[currentIndex];
        try {
            await addReaction({
                userId: currentUser.userId,
                fromUserId: user.id,
                isLike: reactionType === 'like',
                isDisLike: reactionType === 'dislike',
                isSuper: reactionType === 'super',
                likeViewDate: null,
                mutualViewDate: null
            });

            setCurrent(current + 1);
            if (current === num) {
                setPageNumber(pageNumber + 1);
                setCurrent(1);
                loadUsers(); // Fetch new users after reaching 5 swipes
            }
        } catch (error) {
            console.error(`Error adding/updating ${reactionType} reaction:`, error);
        }
    };

    const handleLike = () => {
        handleReaction('like');
        setSwipeClass('swipe-right');
        setTimeout(() => {
            setSwipeClass('');
            setNextClass('fade-in');
            setCurrentIndex((prevIndex) => prevIndex + 1);
        }, 400);
        setDislikeTapped(0);
    };

    const handleDislike = () => {
        handleReaction('dislike');
        setSwipeClass('swipe-left');
        setTimeout(() => {
            setSwipeClass('');
            setNextClass('fade-in');
            setCurrentIndex((prevIndex) => prevIndex + 1);
        }, 400);
        setDislikeTapped(1);
    };

    const handleSuperLike = () => {
        handleReaction('super');
        setSwipeClass('super-swipe-right');
        setTimeout(() => {
            setSwipeClass('');
            setNextClass('fade-in');
            setCurrentIndex((prevIndex) => prevIndex + 1);
        }, 400);
        setDislikeTapped(0);
    };

    const handleBack = () => {
        if (currentIndex > 0 && dislikeTapped === 1) {
            setDislikeTapped(1);
            setSwipeClass('reverse-fade-in');
            setTimeout(() => {
                setSwipeClass('');
                setNextClass('reverse-swipe-left');
                setCurrentIndex((prevIndex) => prevIndex - 1);
            }, 200);
            setDislikeTapped(0);
        } else if (currentIndex === 0 && users.length > 1 && dislikeTapped === 1) {
            setDislikeTapped(1);
            setSwipeClass('reverse-fade-in');
            setTimeout(() => {
                setSwipeClass('');
                setNextClass('reverse-swipe-left');
                setCurrentIndex(users.length - 1);
            }, 200);
            setDislikeTapped(0);
        }
    };

    useEffect(() => {
        const timer = setTimeout(() => {
            setNextClass('');
        }, 500);
        return () => clearTimeout(timer);
    }, [currentIndex]);

    const handlers = useSwipeable({
        onSwipedLeft: () => handleSwiped('Left'),
        onSwipedRight: () => handleSwiped('Right'),
    });

    return (
        <div {...handlers} className="swipe-cards">
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-indicator"></div>
                    <div className="loading-text">{t('please_wait')}</div>
                </div>
            )}
            <img src={jerm} alt="logoAlt" className="logoClass" />
            {users.length > 0 ? (
                users.slice(currentIndex, currentIndex + 1).map((userSub) => (
                    <Card
                        key={user.userId}
                        user={userSub}
                        onLike={handleLike}
                        onDislike={handleDislike}
                        onSuperLike={handleSuperLike}
                        onBack={handleBack}
                        className={`${swipeClass} ${nextClass}`}
                    />
                ))
            ) : (
                !loading && <p>No users available</p>
            )}

            <p>version 2.2</p>
        </div>
    );
};

export default SwipeCards;
