import React, { useEffect, useState, useCallback } from 'react';
import { useTelegram } from '../../hooks/useTelegram';
import { useNavigate } from 'react-router-dom';
import { addReaction } from '../../router/apiServiceFetch';
import { useAuth } from '../../contexts/auth';
import { useTranslation } from '../../contexts/TranslationContext';
import './Styles.scss';
import CardItem from './CardItem';

const LikesWithCards = () => {
    const { t } = useTranslation();
    const { userBase, setUserBase } = useAuth();
    const navigate = useNavigate();
    const [likes, setLikes] = useState([]);
    const { tg, user } = useTelegram();

    // const [user] = useState({ id: 6256940687 });

    const onBack = useCallback(() => {
        navigate('/profile');
    }, [navigate]);

    useEffect(() => {
        if (userBase) {
            const filteredLikes = userBase?.reactions?.filter(reaction => reaction.isLike || reaction.isSuper);
            setLikes(filteredLikes);
        }
    }, [userBase]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
    }, [tg])

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [tg, onBack]);

    const handleReaction = async (reactionType, fromUserId) => {
        try {
            await addReaction({
                userId: fromUserId,
                fromUserId: user.id,
                isLike: reactionType === 'like',
                isDisLike: reactionType === 'dislike',
                isSuper: reactionType === 'super',
                likeViewDate: new Date().toISOString(),
                mutualViewDate: new Date().toISOString()
            });

            setUserBase(prevUserData => ({
                ...prevUserData,
                reactions: prevUserData.reactions.filter(reaction => reaction.fromUserId !== fromUserId && (reaction.isLike || reaction.isSuper))
            }));
        } catch (error) {
            console.error(`Error adding/updating ${reactionType} reaction:`, error);
        }
    };

    const handleLike = (fromUserId) => {
        handleReaction('like', fromUserId);
    };

    const handleDislike = (fromUserId) => {
        handleReaction('dislike', fromUserId);
    };

    return (
        <div className="likes-with-cards">
            <h2>{t('your_likes')}</h2>
            <div className="cards-container">
                {likes?.map((likeUser) => (
                    <CardItem
                        key={likeUser.fromUserId}
                        userId={likeUser.fromUserId}
                        onLike={() => handleLike(likeUser.fromUserId)}
                        onDislike={() => handleDislike(likeUser.fromUserId)}
                    />
                ))}
            </div>
        </div>
    );
};

export default LikesWithCards;
