import React, { useState, useEffect, useCallback } from 'react';
import { VerifyIcon, LocationIcon } from "../../components/GetIcons";
import { useTranslation } from '../../contexts/TranslationContext';
import { useSpring, animated } from '@react-spring/web';
import { useNavigate } from 'react-router-dom';
import './UserProfile.scss';
import jerm from '../../assets/jerm.svg';
import heart_small from '../../assets/icons/heart-small.svg';
import hearts_small from '../../assets/icons/hearts-small.svg';
import { useTelegram } from '../../hooks/useTelegram';
import GetZodiacSign from '../../components/GetZodiacSign';
import CalculateAge from '../../components/CalculateAge';
import { updatePhoto, updateUser, getMutualCount, getLikeCount, insertPhoto } from '../../router/apiServiceFetch';
import { useAuth } from '../../contexts/auth';
import Popup from '../../components/Popup';
import resizeImage from '../../components/ResizeImage';

const UserProfile = () => {
    const { userBase, setUserBase } = useAuth();
    const { t } = useTranslation();
    const [age, setAge] = useState();
    const [fillColor, setFillColor] = useState('#333'); // Default to dark
    const [likeCount, setLikeCount] = useState(0);
    const [mutualCount, setMutualCount] = useState(0);
    const likes = useSpring({ from: { number: 0 }, number: likeCount, delay: 200 });
    const matches = useSpring({ from: { number: 0 }, number: mutualCount, delay: 400 });
    const navigate = useNavigate();
    const { tg } = useTelegram();
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const [loading, setLoading] = useState(false);
    const [editData, setEditData] = useState({
        fullName: userBase.fullName,
        userName: userBase.sex,
        sex: userBase.userName || '',
        birthday: userBase.birthday,
        profession: userBase.profession,
        description: userBase.description,
        country: userBase.country,
        city: userBase.city
    });
    const [photoSlots, setPhotoSlots] = useState(() => {
        const initialPhotos = [...(userBase.photoes || [])];
        while (initialPhotos.length < 3) {
            initialPhotos.push({});
        }
        return initialPhotos;
    });

    useEffect(() => {
        const updatedPhotos = [...(userBase.photoes || [])];
        while (updatedPhotos.length < 3) {
            updatedPhotos.push({});
        }
        setPhotoSlots(updatedPhotos);
    }, [userBase.photoes]);

    const onMatch = useCallback(() => {
        navigate('/match');
    }, [navigate]);

    const onBack = useCallback(() => {
        navigate('/swipes');
    }, [navigate]);

    const loadLikesCount = useCallback(async () => {
        try {
            const likeCountData = await getLikeCount(userBase.userId);
            setLikeCount(Number(likeCountData.likeCount) || 0);
        } catch (error) {
            console.error('Error fetching like count:', error);
        }
    }, [userBase.userId]);

    const loadMutualCount = useCallback(async () => {
        try {
            const mutualCountData = await getMutualCount(userBase.userId);
            setMutualCount(Number(mutualCountData.mutualCount) || 0);
        } catch (error) {
            console.error('Error fetching mutual count:', error);
        }
    }, [userBase.userId]);

    useEffect(() => {
        if (localStorage.getItem('theme') === 'light') {
            setFillColor('#333');
        } else {
            setFillColor('#fff');
        }

        setAge(CalculateAge(new Date(userBase.birthday)));
        loadLikesCount();
        loadMutualCount();
    }, [userBase.birthday, loadLikesCount, loadMutualCount]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }

        if (!tg.SettingsButton.isVisible) {
            tg.SettingsButton.show();
        }
    }, [tg]);

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [onBack, tg]);

    useEffect(() => {
        tg.onEvent('settingsButtonClicked', onMatch);
        return () => {
            tg.offEvent('settingsButtonClicked', onMatch);
        };
    }, [onMatch, tg]);

    const handleFileUpload = async (e, index) => {
        setLoading(true);
        const file = e.target.files[0];
        if (file) {

            try {
                const wantedWidth = 800; // Set wanted width to 800 pixels
                const wantedHeight = 800; // Set wanted height to 800 pixels
                const { base64String, fileName } = await resizeImage(file, wantedWidth, wantedHeight);

                const newPhotos = [...photoSlots];
                newPhotos[index] = { ...newPhotos[index], fileName: fileName, base64Data: base64String };
                const id = newPhotos[index].id;
                if (id > 0) {
                    await updatePhoto({ id, userId: userBase.userId, fileName: fileName, photoBase64: base64String });
                }
                else {
                    await insertPhoto({ id, userId: userBase.userId, fileName: fileName, photoBase64: base64String });
                }

                setPhotoSlots(newPhotos);
                setUserBase(prevUserData => ({
                    ...prevUserData,
                    photoes: newPhotos
                }));
                setLoading(false);

            } catch (error) {
                console.error('Error updating photo:', error);
            }
        };
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditData(prevEditData => ({
            ...prevEditData,
            [name]: value
        }));
    };

    const handleSaveChanges = async () => {
        try {
            setIsPopupOpen(false);
            setLoading(true);
            await updateUser({
                userId: userBase.userId,
                fullName: editData.fullName,
                profession: editData.profession,
                description: editData.description,
                birthday: editData.birthday,
                country: editData.country,
                city: editData.city,
                moderated: true
            });

            setUserBase(prevUserData => ({
                ...prevUserData,
                fullName: editData.fullName,
                profession: editData.profession,
                description: editData.description,
                birthday: editData.birthday,
                country: editData.country,
                city: editData.city
            }));
            setLoading(false);
        } catch (error) {
            console.error('Error updating user data:', error);
            alert('Failed to update profile');
        }
    };

    const openPopup = () => {
        setIsPopupOpen(true);
    };

    const closePopup = () => {
        setIsPopupOpen(false);
    };

    return (
        <div className="user-profile">
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-indicator"></div>
                    <div className="loading-text">{t('please_wait')}</div>
                </div>
            )}
            <img src={jerm} alt="logoAlt" className="logoClass" />
            <div className="profile-content">
                <div className="photo-grid">
                    {photoSlots.map((photo, index) => (
                        <div key={index} className={`photo-box photo-box-${index + 1}`}>
                            <label htmlFor={`upload-${index}`} className="upload-label">
                                {photo.base64Data ? (
                                    <img src={`data:image/jpeg;base64,${photo.base64Data}`} alt={`Upload Preview ${index + 1}`} className="photo-preview" />
                                ) : (
                                    <span>+</span>
                                )}
                            </label>
                            <input
                                id={`upload-${index}`}
                                type="file"
                                accept="image/*"
                                onChange={(e) => handleFileUpload(e, index)}
                                className="upload-input"
                            />
                        </div>
                    ))}
                </div>
                <div className="counters">
                    <div className="counter" onClick={() => navigate('/likes')}>
                        <animated.div>{likes.number.to(n => n.toFixed(0))}</animated.div>
                        <img src={heart_small} alt={'heartAlt'} className="heartClass" />
                    </div>
                    <div className="counter" onClick={() => navigate('/mutuals')}>
                        <animated.div>{matches.number.to(n => n.toFixed(0))}</animated.div>
                        <img src={hearts_small} alt={'heartsAlt'} className="heartClass" />
                    </div>
                </div>
                <div className="profile-info">
                    <div className='info_top'>
                        <h3>{GetZodiacSign(userBase.birthday, fillColor)}{userBase.fullName}, {age} <VerifyIcon className='verify' /></h3>
                        <p><LocationIcon fillColor={fillColor} /> {userBase.country}, {userBase.city}</p>
                    </div>
                    <p className='title'>{t('profession')}: </p>
                    <p className='text'>{userBase.profession}</p>
                    <p className='title'>{t('description')}: </p>
                    <p className='text'>{userBase.description}</p>
                    <button onClick={openPopup} className="edit-button">{t('edit_profile')}</button>
                </div>
            </div>
            <Popup
                isOpen={isPopupOpen}
                onClose={closePopup}
                onSave={handleSaveChanges}
                editData={editData}
                handleInputChange={handleInputChange}
            />
        </div>
    );
};

export default UserProfile;
