import React, { useState, useEffect, createContext, useContext, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { getUser, loginUser } from '../router/apiServiceFetch';
import { useTelegram } from "../hooks/useTelegram";


function AuthProvider(props) {
  const navigate = useNavigate();
  const [userBase, setUserBase] = useState();
  const [loading, setLoading] = useState(true);
  const { tg, user } = useTelegram();

  useEffect(() => {
    const initializeWebApp = () => {
      tg.ready();
      tg.expand();
      tg.enableClosingConfirmation();
    };

    initializeWebApp();
    (async function () {
      const result = await loginUser(user.id);
      if (result.isSuccess) {
        localStorage.setItem('token', result.token);
        localStorage.setItem('refreshToken', result.refreshToken);

        const userResponse = await getUser(user.id);
        setUserBase(userResponse);
        navigate('/swipes');

      }

      setLoading(false);
    })();
  }, []);


  const signIn = useCallback(async (userId) => {
    const result = await loginUser(userId);
    if (result.isSuccess) {
      localStorage.setItem('token', result.token);
      localStorage.setItem('refreshToken', result.refreshToken);

      const userResponse = await getUser(userId);
      setUserBase(userResponse);
      return userResponse;
    }
  }, []);

  const signOut = useCallback(() => {
    setUserBase(undefined);
  }, []);


  return (
    <AuthContext.Provider value={{ userBase, setUserBase, signIn, signOut, loading }} {...props} />
  );
}

const AuthContext = createContext({ loading: false });
const useAuth = () => useContext(AuthContext);

export { AuthProvider, useAuth }
