// Get all users excluding the current user
export const getAllUsers = async (userId, pageNumber = 1, pageSize = 10, country = null, city = null) => {
    try {
        let url = `/user/getAll?userId=${userId}&pageNumber=${pageNumber}&pageSize=${pageSize}`;
        if (country) {
            url += `&country=${country}`;
        }
        if (city) {
            url += `&city=${city}`;
        }
        const response = await fetchApi(url, {
            method: 'GET',
        });
        return response;
    } catch (error) {
        console.error('Get all users error:', error);
        throw error;
    }
};

