import React, { useState, useEffect } from 'react';
import { CancelIcon, CheckIcon, WriteIcon, VerifyIcon, LocationIcon } from "../../components/GetIcons.js";
import GetZodiacSign from '../../components/GetZodiacSign.js';
import CalculateAge from '../../components/CalculateAge.js';
import { useTranslation } from '../../contexts/TranslationContext';
import { getPhotosByUser, getUser } from '../../router/apiServiceFetch';
import IconButton from '../../components/IconButton.js';
import PhotoModal from '../../components/PhotoModal.js'; // Import the modal component
import './CardItem.scss';

const CardItem = ({ userId, isMatch, onLike, onDislike, onWrite }) => {
    const { t } = useTranslation();
    const [fillColor, setFillColor] = useState();
    const [user, setUser] = useState();
    const [age, setAge] = useState();
    const [photos, setPhotos] = useState([]);
    const [showModal, setShowModal] = useState(false);

    const handleViewAllPhotos = async () => {
        try {
            const photosResponse = await getPhotosByUser(userId);
            setShowModal(true);
        } catch (error) {
            console.error('Error fetching photos:', error);
        }
    };

    useEffect(() => {
        const loadUser = async () => {
            try {
                const userResponse = await getUser(userId);
                setUser(userResponse);
                setPhotos(userResponse?.photoes);
                setAge(CalculateAge(userResponse.birthday));
            } catch (error) {
                console.error('Error fetching user:', error);
            }
        };

        if (localStorage.getItem('theme') === 'light') {
            setFillColor('#333');
        } else {
            setFillColor('#fff');
        }
        loadUser();
    }, [userId]);

    return (
        // eslint-disable-next-line react/jsx-no-useless-fragment
        !user ? <></> :
            <div className='card-item'>
                <div className="photo-container" onClick={handleViewAllPhotos}>
                    <img src={`data:image/${photos[0]?.fileExtension};base64,${photos[0]?.base64Data}`} alt={'photo'} className="photo" />
                </div>
                <div className="info">
                    <>
                        <h2>
                            {GetZodiacSign(user.birthday, fillColor)}
                            {user.fullName}, {age}
                            <VerifyIcon className='verify' />
                        </h2>
                        <h3>
                            <LocationIcon fillColor={fillColor} />
                            {user.city}
                        </h3>
                        <h4>{user.profession}</h4>
                        <p>{user.description}</p>
                    </><div className="buttons match">
                        {isMatch ? (
                            <button className="chat-button" onClick={onWrite}>{t('begin_chat')}</button>
                        ) : (
                            <>
                                <IconButton icon={CancelIcon} onClick={onDislike} className="dislike" />
                                <IconButton icon={CheckIcon} onClick={onLike} className="like" />
                            </>
                        )}
                    </div>

                </div>
                {showModal && (
                    <PhotoModal
                        photos={photos}
                        onClose={() => setShowModal(false)}
                    />
                )}
            </div>
    );
};

export default CardItem;
