import React, { useRef, useEffect, useState } from 'react';
import CountrySelect from './CountrySelect';
import CitySelect from './CitySelect';
import { useTranslation } from '../contexts/TranslationContext';
import './Popup.scss';
import { fetchCountryCityData } from '../router/apiServiceFetch';

const Popup = ({ isOpen, onClose, onSave, editData, handleInputChange }) => {
    const { t } = useTranslation();
    const [countryCityData, setCountryCityData] = useState([]);
    const textareaRef = useRef(null);

    const autoResizeTextarea = () => {
        if (textareaRef.current) {
            textareaRef.current.style.height = 'auto';
            textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
        }
    };
    const formatDate = (date) => {
        const d = new Date(date);
        const year = d.getFullYear();
        const month = ('0' + (d.getMonth() + 1)).slice(-2);
        const day = ('0' + d.getDate()).slice(-2);
        return `${year}-${month}-${day}`;
    };
    useEffect(() => {
        const fetchCountryCityDataAsync = async () => {
            try {
                const data = await fetchCountryCityData();
                setCountryCityData(data);
            } catch (error) {
                console.error('Error fetching countries and cities:', error);
            }
        };

        fetchCountryCityDataAsync();
    }, []);
    
    useEffect(() => {
        autoResizeTextarea();
    }, [editData.description]);
    if (!isOpen) return null;
    return (
        <div className="popup-overlay">
            <div className="popup">
                <h2>{t('edit_profile')}</h2>
                <div>
                    <label htmlFor="name">{t('name')}</label>
                    <input id="name" type="text" name="fullName" value={editData.fullName} onChange={handleInputChange} />
                </div>
                <div>
                    <label htmlFor="profession">{t('profession')}</label>
                    <input id="profession" type="text" name="profession" value={editData.profession} onChange={handleInputChange} />
                </div>
                <div>
                    <label htmlFor="description">{t('description')}</label>
                    <textarea
                        id="description"
                        className="textArea"
                        name="description"
                        value={editData.description}
                        onChange={handleInputChange}
                        maxLength="120"
                        placeholder={t('descriptionMAX')}
                        ref={textareaRef}
                    />
                </div>
                <div>
                    <label htmlFor="birthday">{t('dateOfBirth')}</label>
                    <input id="birthday" type="date" name="birthday" value={formatDate(editData.birthday)} onChange={handleInputChange} />
                </div>
                <CountrySelect id="country" formData={editData} handleChange={handleInputChange} t={t} countryCityData={countryCityData} />
                <CitySelect id="region" formData={editData} handleChange={handleInputChange} t={t} countryCityData={countryCityData} />
                <div className="popup-buttons">
                    <button onClick={onSave} className="save-button">{t('save')}</button>
                    <button onClick={onClose} className="cancel-button">{t('cancel')}</button>
                </div>
            </div>
        </div>
    );
};

export default Popup;
