import React from 'react';
import { AriesIcon, TaurusIcon, GeminiIcon, CancerIcon, LeoIcon, VirgoIcon, LibraIcon, ScorpioIcon, SagittariusIcon, CapricornIcon, AquariusIcon, PiscesIcon } from "./GetIcons.js";

const GetZodiacSign = (birthday, fillColor) => {
    const birthdayDate = new Date(birthday);
    const day = birthdayDate.getDate();
    const month = birthdayDate.getMonth() + 1;

    if ((month == 3 && day >= 21) || (month == 4 && day <= 19)) {
        return <AriesIcon fillColor={fillColor} />;
    } else if ((month == 4 && day >= 20) || (month == 5 && day <= 20)) {
        return <TaurusIcon fillColor={fillColor} />;
    } else if ((month == 5 && day >= 21) || (month == 6 && day <= 20)) {
        return <GeminiIcon fillColor={fillColor} />;
    } else if ((month == 6 && day >= 21) || (month == 7 && day <= 22)) {
        return <CancerIcon fillColor={fillColor} />;
    } else if ((month == 7 && day >= 23) || (month == 8 && day <= 22)) {
        return <LeoIcon fillColor={fillColor} />;
    } else if ((month == 8 && day >= 23) || (month == 9 && day <= 22)) {
        return <VirgoIcon fillColor={fillColor} />;
    } else if ((month == 9 && day >= 23) || (month == 10 && day <= 22)) {
        return <LibraIcon fillColor={fillColor} />;
    } else if ((month == 10 && day >= 23) || (month == 11 && day <= 21)) {
        return <ScorpioIcon fillColor={fillColor} />;
    } else if ((month == 11 && day >= 22) || (month == 12 && day <= 21)) {
        return <SagittariusIcon fillColor={fillColor} />;
    } else if ((month == 12 && day >= 22) || (month == 1 && day <= 19)) {
        return <CapricornIcon fillColor={fillColor} />;
    } else if ((month == 1 && day >= 20) || (month == 2 && day <= 18)) {
        return <AquariusIcon fillColor={fillColor} />;
    } else if ((month == 2 && day >= 19) || (month == 3 && day <= 20)) {
        return <PiscesIcon fillColor={fillColor} />;
    }
    return null;
};

export default GetZodiacSign;
