import React, { useState, useEffect, useCallback } from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
import { useNavigate } from 'react-router-dom';
import './PhotoUpload.scss';
import jerm from '../../assets/jerm.svg';
import { useTelegram } from '../../hooks/useTelegram';
import { insertPhoto } from '../../router/apiServiceFetch';
import resizeImage from '../../components/ResizeImage';


function PhotoUpload() {
    const [photos, setPhotos] = useState([null, null, null]);
    const [submitted, setSubmitted] = useState(false);
    const [loading, setLoading] = useState(false);
    const { t } = useTranslation();
    const navigate = useNavigate();
    const { tg, user } = useTelegram();

    //const [user, setUser] = useState({ id: 6256940687 });

    const onBack = useCallback(() => {
        navigate('/register');
    }, [navigate]);

    const onSendData = useCallback(() => {
        handleSubmit();
    }, [photos]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }
    }, [tg]);

    useEffect(() => {
        tg.MainButton.setParams({
            text: t('continue')
        });
        checkAllPhotosFilled();
    }, [photos, t, tg]);

    useEffect(() => {
        tg.onEvent('mainButtonClicked', onSendData);
        return () => {
            tg.offEvent('mainButtonClicked', onSendData);
        };
    }, [onSendData, tg]);

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [onBack, tg]);

    const handleFileUpload = async (e, index) => {
        const file = e.target.files[0];
        if (file) {
            try {
                const wantedWidth = 800; // Set wanted width to 800 pixels
                const wantedHeight = 800; // Set wanted height to 800 pixels
                const { base64String, fileName, fileExtension } = await resizeImage(file, wantedWidth, wantedHeight);
                setPhotos(prevPhotos => {
                    const newPhotos = [...prevPhotos];
                    newPhotos[index] = { base64: base64String, fileName, extension: fileExtension };
                    return newPhotos;
                });
            } catch (error) {
                console.error('Error resizing photo:', error);
            }
        }
    };

    const checkAllPhotosFilled = () => {
        if (photos.every(photo => photo !== null)) {
            tg.MainButton.show();
        } else {
            tg.MainButton.hide();
        }
    };

    const handleSubmit = async () => {
        setLoading(true);
        setSubmitted(true);

        try {
            const userId = user.id;

            for (const photo of photos) {
                if (photo) {
                    await insertPhoto({ userId, fileName: photo.fileName, photoBase64: photo.base64 });
                }
            }

            navigate('/verification');
        } catch (error) {
            console.error('Error uploading photos:', error);
            setLoading(false);
        }
    };

    return (
        <div className="photo-upload">
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-indicator"></div>
                    <div className="loading-text">{t('please_wait')}</div>
                </div>
            )}
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h1>{t('upload_photos')}</h1>
            <form>
                <div className="photo-grid">
                    {photos.map((photo, index) => (
                        <div key={index} className={`photo-box photo-box-${index + 1} ${submitted && !photo ? 'photo-box-missing' : ''}`}>
                            <label htmlFor={`upload-${index}`} className="upload-label">
                                {photo ? (
                                    <img src={`data:image/${photo.extension};base64,${photo.base64}`} alt={`Upload Preview ${index + 1}`} className="photo-preview" />
                                ) : (
                                    <span>+</span>
                                )}
                            </label>
                            <input
                                id={`upload-${index}`}
                                type="file"
                                accept="image/*"
                                onChange={(e) => handleFileUpload(e, index)}
                                className="upload-input"
                            />
                        </div>
                    ))}
                </div>
                {/* <div className="button-container">
                    <button type="button" className="button" onClick={handleSubmit}>
                        go
                    </button>
                </div> */}
            </form>
        </div>
    );
}

export default PhotoUpload;
