import React, { useState, useRef, useEffect, useCallback } from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
import { useNavigate } from 'react-router-dom';
import { useTelegram } from "../../hooks/useTelegram";
import CountrySelect from '../../components/CountrySelect';
import CitySelect from '../../components/CitySelect';
import { registerUser, fetchCountryCityData } from '../../router/apiServiceFetch';
import { useAuth } from '../../contexts/auth';
import './RegistrationForm.scss';
import jerm from '../../assets/jerm.svg';

function RegistrationForm() {
    const { tg, user } = useTelegram();
    const { signIn } = useAuth();
    // const [user, setUser] = useState({ id: 6256940687 });
    const full_name = user?.first_name + (user?.last_name ? ' ' + user.last_name : '');
    const [formData, setFormData] = useState({
        name: full_name || '',
        description: '',
        dateOfBirth: '2000-01-01',
        country: 'Armenia',
        city: 'Yerevan',
        profession: '',
        gender: ''
    });

    const [countryCityData, setCountryCityData] = useState([]);
    const [loading, setLoading] = useState(false);
    const { t } = useTranslation();
    const textareaRef = useRef(null);
    const navigate = useNavigate();

    const onSendData = useCallback(async () => {
        try {

            setLoading(true);
            const userPayload = {
                userId: user.id,
                fullName: formData.name,
                userName: user?.username ? user.username : 'no username',
                sex: formData.gender === 'male' ? 1 : 2,
                birthday: new Date(formData.dateOfBirth),
                country: formData.country,
                city: formData.city,
                profession: formData.profession,
                description: formData.description,
                moderated: false
            };

            const response = await registerUser(userPayload);

            if (response.isSuccess) {
                const userResponse = await signIn(user.id);
                if (userResponse) {
                    navigate('/photo-upload');
                } else {
                    setLoading(false);
                    alert('Failed to log in user. Please try again.');
                }
            } else {
                setLoading(false);
                alert('Failed to create user. Please try again.');
            }
        } catch (error) {
            setLoading(false);
            alert(error.message || 'There was an error creating your profile. Please try again.');
        }
    }, [formData, user, tg, navigate]);

    const onBack = useCallback(() => {
        navigate('/');
    }, [navigate]);

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [onBack, tg]);

    useEffect(() => {
        tg.onEvent('mainButtonClicked', onSendData);
        return () => {
            tg.offEvent('mainButtonClicked', onSendData);
        };
    }, [onSendData, tg]);

    useEffect(() => {
        const fetchCountryCityDataAsync = async () => {
            try {
                const data = await fetchCountryCityData();
                setCountryCityData(data);
            } catch (error) {
                console.error('Error fetching countries and cities:', error);
            }
        };

        autoResizeTextarea();
        checkAllFieldsFilled();
        fetchCountryCityDataAsync();
    }, []);

    useEffect(() => {
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
        if (tg.SettingsButton.isVisible) {
            tg.SettingsButton.hide();
        }
    }, [tg]);

    useEffect(() => {
        tg.MainButton.setParams({
            text: t('submit')
        });
        checkAllFieldsFilled();
    }, [formData, tg, t]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });

        if (name === 'description') {
            autoResizeTextarea();
        }
    };

    const autoResizeTextarea = () => {
        if (textareaRef.current) {
            textareaRef.current.style.height = 'auto';
            textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
        }
    };

    const checkAllFieldsFilled = () => {
        const { name, description, dateOfBirth, city, profession, gender } = formData;
        const allFilled = name && description && dateOfBirth && city && profession && gender;
        if (allFilled && !tg.MainButton.isVisible) {
            tg.MainButton.show();
        }
    };

    return (
        <div className="registration-form">
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-indicator"></div>
                    <div className="loading-text">{t('please_wait')}</div>
                </div>
            )}
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h2>{t('fill_data')}</h2>
            <form>
                <div>
                    <label htmlFor="name">{t('name')}</label>
                    <input id="name" type="text" name="name" value={formData.name} onChange={handleChange} required />
                </div>
                <div>
                    <label htmlFor="profession">{t('profession')}</label>
                    <input id="profession" type="text" name="profession" value={formData.profession} onChange={handleChange} required />
                </div>
                <div>
                    <label htmlFor="description">{t('description')}</label>
                    <textarea
                        id="description"
                        className="textArea"
                        name="description"
                        value={formData.description}
                        onChange={handleChange}
                        maxLength="120"
                        placeholder={t('descriptionMAX')}
                        ref={textareaRef}
                        required
                    />
                </div>
                <div>
                    <label htmlFor="dateOfBirth">{t('dateOfBirth')}</label>
                    <input id="dateOfBirth" type="date" name="dateOfBirth" value={formData.dateOfBirth} onChange={handleChange} required />
                </div>
                <CountrySelect id="country" formData={formData} handleChange={handleChange} t={t} countryCityData={countryCityData} />
                <CitySelect id="region" formData={formData} handleChange={handleChange} t={t} countryCityData={countryCityData} />
                <div className="gender">
                    <div className={`gender-option ${formData.gender === 'male' ? 'active male' : 'male'}`}>
                        <input id="male" type="radio" name="gender" value="male" checked={formData.gender === 'male'} onChange={handleChange} required />
                        <label htmlFor="male">{t('male')}</label>
                    </div>
                    <div className={`gender-option ${formData.gender === 'female' ? 'active female' : 'female'}`}>
                        <input id="female" type="radio" name="gender" value="female" checked={formData.gender === 'female'} onChange={handleChange} required />
                        <label htmlFor="female">{t('female')}</label>
                    </div>
                </div>
                {/* {<div className="button-container">
                    <button type="button" className="back-button" onClick={onSendData}>
                        {t('register')}
                    </button>
                </div>} */}
            </form>
        </div>
    );
}

export default RegistrationForm;
