import React, { useState, useEffect, useCallback } from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
import { useNavigate } from 'react-router-dom';
import './SelfieUpload.scss';
import jerm from '../../assets/jerm.svg';
import { useTelegram } from '../../hooks/useTelegram';
import { insertSelfie } from '../../router/apiServiceFetch';
import resizeImage from '../../components/ResizeImage';

function SelfieUpload() {
    const [selfie, setSelfie] = useState(null);
    const [loading, setLoading] = useState(false);
    const { t } = useTranslation();
    // const { tg } = useTelegram();
    const { tg, user } = useTelegram();

    // const [user, setUser] = useState({ id: 6256940687 });
    const navigate = useNavigate();

    const onBack = useCallback(() => {
        navigate('/verification');
    }, [navigate]);

    const onSendData = useCallback(() => {
        handleSubmit();
    }, [selfie]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
        if (tg.MainButton.isVisible) {
            tg.MainButton.hide();
        }
    }, [tg.BackButton, tg.MainButton]);

    useEffect(() => {
        tg.MainButton.setParams({
            text: t('continue')
        });
        checkAllSelfieFilled();
    }, [tg.MainButton, t, selfie]);

    useEffect(() => {
        tg.onEvent('mainButtonClicked', onSendData);
        return () => {
            tg.offEvent('mainButtonClicked', onSendData);
        };
    }, [tg, onSendData]);

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [tg, onBack]);

    const handleFileUpload = async (e, index) => {
        const file = e.target.files[0];
        if (file) {
            try {
                const wantedWidth = 800; // Set wanted width to 800 pixels
                const wantedHeight = 800; // Set wanted height to 800 pixels
                const { base64String, fileName, fileExtension } = await resizeImage(file, wantedWidth, wantedHeight);
                setSelfie({ base64: base64String, fileName, extension: fileExtension });
            } catch (error) {
                console.error('Error resizing photo:', error);
            }
            // reader.readAsDataURL(file);
        }
    };

    const checkAllSelfieFilled = () => {
        if (selfie) {
            tg.MainButton.show();
        } else {
            tg.MainButton.hide();
        }
    };

    const handleSubmit = async () => {
        setLoading(true);
        try {
            const userId = user.id;

            if (selfie) {
                await insertSelfie({ userId, fileName: selfie.fileName, photoBase64: selfie.base64 });
            }

            navigate('/waiting-for-moderation');
        } catch (error) {
            console.error('Error uploading selfie:', error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="selfie-upload">
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-indicator"></div>
                    <div className="loading-text">{t('please_wait')}</div>
                </div>
            )}
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h1>{t('selfie_upload_title')}</h1>
            <p>{t('selfie_upload_instructions')}</p>
            <form>
                <div className="selfie-box">
                    {selfie ? (
                        <>
                            <img src={`data:image/${selfie.extension};base64,${selfie.base64}`} alt="Selfie Preview" className="selfie-preview" />
                            <label htmlFor="upload-selfie" className="upload" />
                        </>
                    ) : (
                        <label htmlFor="upload-selfie" className="upload-label">
                            <span>+</span>
                        </label>
                    )}
                    <input
                        id="upload-selfie"
                        type="file"
                        accept="image/*"
                        onChange={handleFileUpload}
                        className="upload-input"
                    />
                </div>
                {/* <div className="button-container">
                    <button type="button" className="button" onClick={handleSubmit}>
                        {t('register')}
                    </button>
                </div> */}
            </form>
        </div>
    );
}

export default SelfieUpload;
