import React, { useState, useEffect } from 'react';
import { CancelIcon, CheckIcon, BackIcon, StarIcon, VerifyIcon, LocationIcon } from "../../components/GetIcons.js";
import GetZodiacSign from '../../components/GetZodiacSign.js';
import CalculateAge from '../../components/CalculateAge.js';
import { getPhotosByUser } from '../../router/apiServiceFetch';
import IconButton from '../../components/IconButton.js';
import './Card.scss';

const Card = ({ user, onLike, onDislike, onSuperLike, onBack, className }) => {
    const [currentPhotoIndex, setCurrentPhotoIndex] = useState(0);
    const [fillColor, setFillColor] = useState();
    const [theme, setTheme] = useState(); // Default theme
    const [photos, setPhotos] = useState(user.photoes);
    const age = CalculateAge(user.birthday);

    const handleNextPhoto = () => {
        if (currentPhotoIndex < photos?.length - 1) {
            setCurrentPhotoIndex(currentPhotoIndex + 1);
        } else {
            setCurrentPhotoIndex(0);
        }
    };

    useEffect(() => {
        setTheme(localStorage.getItem('theme'));
    }, []);

    useEffect(() => {
        if (localStorage.getItem('theme') === 'light') {
            setFillColor('#333');
        } else {
            setFillColor('#fff');
        }
    }, [user]);

    return (
        <div className={`card ${className}`}>
            <div className="photo-container" onClick={handleNextPhoto}>
                {photos?.length > 0 ? (
                    <img src={photos[currentPhotoIndex]} alt="user" className="photo" />
                ) : (
                    <div className="no-photo">No photo available</div>
                )}
            </div>
            <div className="dots-container">
                {photos?.map((_, index) => (
                    <span key={index} className={`dot ${theme} ${currentPhotoIndex === index ? 'active' : ''}`}></span>
                ))}
            </div>
            <div className="info">
                <h2>
                    {GetZodiacSign(user.birthday, fillColor)}
                    {user.fullName}, {age}
                    <VerifyIcon className='verify' />
                </h2>
                <h3>
                    <LocationIcon fillColor={fillColor} />
                    {user.city}
                </h3>
                <h4>{user.profession}</h4>
                <p>{user.description}</p>
            </div>
            <div className="buttons">
                <IconButton icon={CancelIcon} onClick={onDislike} className="dislike" />
                <IconButton icon={BackIcon} onClick={onBack} className="back" />
                <IconButton icon={StarIcon} onClick={onSuperLike} className="superlike" />
                <IconButton icon={CheckIcon} onClick={onLike} className="like" />
            </div>
        </div>
    );
};

export default Card;
