import React, { useEffect, useCallback } from 'react';
import { useTranslation } from '../../contexts/TranslationContext';
import { useNavigate } from 'react-router-dom';
import './VerificationPage.scss';
import jerm from '../../assets/jerm.svg';
import { useTelegram } from '../../hooks/useTelegram';


function VerificationPage() {
    const { t } = useTranslation();
    const { tg } = useTelegram();
    const navigate = useNavigate();

    const onBack = useCallback(() => {
        navigate('/photo-upload');
    }, [])

    const onSendData = useCallback(() => {
        navigate('/selfie-upload');
    }, [])

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
        tg.MainButton.setParams({
            text: t('verification_button')
        });
    }, [])

    useEffect(() => {
        tg.onEvent('mainButtonClicked', onSendData)
        return () => {
            tg.offEvent('mainButtonClicked', onSendData)
        }
    }, [onSendData])

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack)
        return () => {
            tg.offEvent('backButtonClicked', onBack)
        }
    }, [onBack])


    return (
        <div className="verification-page">
            <img src={jerm} alt={t('logoAlt')} className="logo" />
            <h1>{t('verification_title')}</h1>
            <p>{t('verification_text')}</p>
            {/* <div className="button-container">
                <button type="button" className="button" onClick={navigate('/selfie-upload')}>
                    {t('register')}
                </button>
            </div> */}
        </div>
    );
}

export default VerificationPage;
