import React, { useEffect, useState, useCallback } from 'react';
import { useTelegram } from '../../hooks/useTelegram';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/auth';
import { useTranslation } from '../../contexts/TranslationContext';
import './Styles.scss';
import CardItem from './CardItem';

const MatchWithCards = () => {
    const { t } = useTranslation();
    const { userBase } = useAuth();
    const navigate = useNavigate();
    const [likes, setLikes] = useState([]);
    const { tg, user } = useTelegram();

    // const [user] = useState({ id: 6256940687 });

    const onBack = useCallback(() => {
        navigate('/profile');
    }, [navigate]);

    useEffect(() => {
        if (userBase) {
            const filteredLikes = userBase?.reactions?.filter(reaction => reaction.mutual);
            setLikes(filteredLikes);
        }
    }, [userBase]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
    }, [tg])

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [tg, onBack]);


    const handleWrite = useCallback((fromUserId) => {
        const data = {
            userId: fromUserId
        };
        tg.sendData(JSON.stringify(data));
    }, [tg]);


    return (
        <div className="likes-with-cards">
            <h2>{t('mutuals_likes')}</h2>
            <div className="cards-container">
                {likes?.map((likeUser) => (
                    <CardItem
                        key={likeUser.fromUserId}
                        userId={likeUser.fromUserId}
                        isMatch={true}
                        onWrite={() => handleWrite(likeUser.fromUserId)}
                    />
                ))}
            </div>
        </div>
    );
};

export default MatchWithCards;
